import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { SourceWarehouse } from '../../../source/domain-api/source.warehouse';
import { SmartComponent } from '../../../../common/cdk/smart-component';
import { ItemEntity } from '../../../source/domain/core/item/item.entity';
import { StructureId } from '../../../core/domain/structure.id';
import { ListViewTemplate } from './template/list-view-template';
import { ListViewTemplateArchive } from './template/list-view-template.archive';

@Component({
	selector: 'gui-list-view-source',
	template: `

		<gui-list-view-item *ngFor="let element of source"
							[item]="element"
							[template]="template">
		</gui-list-view-item>

		<gui-empty-source [items]="source">
		</gui-empty-source>

	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	host: {
		'[class.gui-list-view-source]': `"true"`
	}
})
export class ListViewSourceComponent extends SmartComponent implements OnInit {

	source: Array<any> = [];

	template: ListViewTemplate;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly structureSourceWarehouse: SourceWarehouse,
				private readonly containerTemplateArchive: ListViewTemplateArchive) {
		super();
	}

	ngOnInit() {
		this.structureSourceWarehouse
			.onEntities(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((items: Array<ItemEntity>) => {
				this.source = items.map(i => i.getData());
				this.changeDetectorRef.detectChanges();
			});

		this.containerTemplateArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((template: ListViewTemplate) => {
				this.template = template;
				this.changeDetectorRef.detectChanges();
			});
	}

}
