import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { ActiveFiltersSetEvent } from '../active-filters-set.event';
import { ActiveFilterReadModel } from '../../../domain-api/active/active-filter.read-model';


export class FilterRemovedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly activeFilters: ReadonlyArray<ActiveFilterReadModel>) {
		super(aggregateId, 'FilterRemovedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new ActiveFiltersSetEvent(
			this.getAggregateId(),
			this.activeFilters
		);
	}

}
