export class SelectorVisibility {

	enabled: boolean = null;

	visible: boolean = false;

	constructor() {
	}

	isVisible(): boolean {

		if (this.enabled !== null) {
			return this.enabled;
		}

		return this.visible;
	}

	setEnabled(enabled: boolean): void {
		this.enabled = enabled;
	}

	setVisible(visible: boolean): void {
		this.visible = visible;
	}

}
