import { Command } from '@generic-ui/hermes';

import { QuickFiltersConfig } from '../../domain-api/quick-filters.config';

import { StructureId } from '../../../core/domain/structure.id';


export class SetConfigQuickFilterCommand extends Command {

	constructor(structureId: StructureId,
				private readonly quickFiltersConfig: QuickFiltersConfig) {
		super(structureId, 'SetConfigQuickFilterCommand');
	}

	getConfig(): QuickFiltersConfig {
		return this.quickFiltersConfig;
	}

}
