import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';
import { SelectedRowChangedEvent } from './selected-row-changed.event';

export class SelectedRowChangedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(structureId: StructureId,
				private readonly selectedRows: Array<string>,
				private readonly allSelected: boolean,
				private readonly allUnselected: boolean) {
		super(structureId, 'SelectedRowChangedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new SelectedRowChangedEvent(this.getAggregateId(), this.selectedRows, this.allSelected, this.allUnselected);
	}
}
