import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FilterTypeReadModel } from '../../domain-api/type/filter-type.read-model';
import { FilterTypeId } from '../../domain/type/filter-type.id';
import { PureComponent } from '../../../../common/cdk/pure-component';

@Component({
	selector: 'gui-filter-type-selector[filterTypes]',
	template: `

		<ng-container>
			Filter Type:
		</ng-container>

		<gui-dropdown>
			<gui-dropdown-item *ngFor="let filterType of filterTypes"
							   (click)="onSelectChange(filterType)">
				{{filterType.getName()}}
			</gui-dropdown-item>
		</gui-dropdown>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FilterTypeSelectorComponent extends PureComponent {

	@Input()
	filterTypes: Array<FilterTypeReadModel>;

	@Output()
	filterTypeSelected = new EventEmitter<FilterTypeId>();

	onSelectChange(filterType: FilterTypeReadModel): void {
		this.filterTypeSelected.emit(filterType.getId());
	}
}
