import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { UniqueValue } from '../unique-value';

export class UniqueFilterCalculatedEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly map: Map<string, Array<UniqueValue>>) {
		super(aggregateId, 'UniqueFilterCalculatedEvent');
	}

	getUniqueValues(): Map<string, Array<UniqueValue>> {
		return this.map;
	}

}
