import { ChangeDetectorRef, Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';

import { StructureCommandDispatcher } from '../../../domain-api/structure.command-dispatcher';
import { CellTemplateWithContext } from '../../../../../composition/domain-api/read/definition/cell-template-with-context';
import { SmartComponent } from '../../../../../common/cdk/smart-component';
import { CompositionWarehouse } from '../../../../../composition/domain-api/composition.warehouse';

import { StructureColumnMenuConfigArchive } from './config/structure.column-menu-config.archive';
import { StructureColumnMenuConfig } from './config/structure.column-menu-config';
import { FilterWarehouse } from '../../../../filter/domain-api/filter.warehouse';
import { StructureId } from '../../../domain/structure.id';
import { UniqueValuesReadModel } from '../../../../filter/domain-api/unique/unique-values.read-model';


@Component({
	template: `
		<div *ngIf="isEnabled()"
			 class="gui-header-menu-tab">

			<gui-tab [menu]="config.getMenus()" [active]="config.getActiveMenu()">

				<ng-container *ngIf="config.isMainEnabled()">

					<gui-tab-item class="gui-tab-item-dropdown" [tab]="config.getMainMenu()">

						<gui-structure-column-config-sort *ngIf="column.isSortEnabled()"
														  [column]="column">
						</gui-structure-column-config-sort>

						<gui-structure-column-config-column-hide
								[column]="column">
						</gui-structure-column-config-column-hide>

						<gui-structure-column-config-column-move
								[column]="column">
						</gui-structure-column-config-column-move>

					</gui-tab-item>

				</ng-container>

				<ng-container *ngIf="config.isFilteringEnabled()">

					<gui-tab-item [tab]="config.getFilterMenu()">

						<!--						<div class="gui-header-menu-item">{{config.getFilterMenu()}}</div>-->

						<gui-unique-value-list
								[fieldId]="column.getFieldId()">
						</gui-unique-value-list>

					</gui-tab-item>

				</ng-container>

				<ng-container *ngIf="config.isColumnManagerEnabled()">

					<gui-tab-item [tab]="config.getColumnMenu()">

						<gui-structure-menu-column-manager>
						</gui-structure-menu-column-manager>

					</gui-tab-item>

				</ng-container>

			</gui-tab>
		</div>
	`
})
export class StructureColumnConfigComponent extends SmartComponent implements OnInit {

	@ViewChild('headerSortMenu', { static: false })
	headerSortMenu: ElementRef;

	config: StructureColumnMenuConfig;

	uniqueValues: Array<any> = [];

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly structureCommandService: StructureCommandDispatcher,
				private readonly compositionReadModelService: CompositionWarehouse,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				@Inject('column') public readonly column: CellTemplateWithContext) {
		super();
	}

	ngOnInit() {

		this.filterWarehouse
			.onUniqueValues(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((uniqueValuesReadModel: UniqueValuesReadModel) => {
				this.uniqueValues = uniqueValuesReadModel.getValues(this.column.getFieldId());
				this.changeDetectorRef.detectChanges();
			});

		this.structureColumnMenuConfigArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((config: StructureColumnMenuConfig) => {
				this.config = config;
				this.changeDetectorRef.detectChanges();
			});
	}

	isEnabled(): boolean {
		return this.config && this.config.isEnabled();
	}
}
