import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';
import { Field } from '../core/field/field';
import { FieldsInitedEvent } from './fields-inited.event';
import { FieldConfig } from '../../domain-api/field.config';


export class FieldsInitedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(structureId: StructureId,
				private readonly fieldConfigs: Array<FieldConfig>, // TODO merge
				private readonly fields: Array<Field>) {
		super(structureId, 'FieldsInitedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {

		return new FieldsInitedEvent(
			this.getAggregateId(),
			this.fieldConfigs,
			this.fields
		);
	}

	getFieldConfigs(): Array<FieldConfig> {
		return this.fieldConfigs;
	}

	getFields(): Array<Field> {
		return this.fields;
	}
}
