import { ContentChild, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';

import { GuiListItemComponent } from './item/gui.list-item.component';

import { GuiListField, GuiListPaging, GuiListSearching, GuiListView } from '../domain-api/gui.list.public-api';
import { GuiListPagingConverter } from './paging/gui-list.paging.converter';
import { GuiListCardComponent } from './card/gui.list-card.component';
import { GuiListModeConverter } from './mode/gui-list.mode.converter';

import { ListViewCardTemplate } from '../../../structure/list/feature/card/template/list-view.card-template';
import { ListViewTemplate } from '../../../structure/list/feature/source/template/list-view-template';
import { ListViewMode } from '../../../structure/list/domain/mode/list-view-mode';
import { FieldConfig } from '../../../structure/field/domain-api/field.config';
import { SearchConfig } from '../../../structure/search/domain-api/search-config';


export abstract class GuiListGateway implements OnChanges {

	@ContentChild(GuiListItemComponent, { static: true })
	listItem: GuiListItemComponent;

	@ContentChild(GuiListCardComponent, { static: true })
	listCard: GuiListCardComponent;

	@Input()
	source: Array<any>;

	@Input()
	template: (item: any) => string;

	@Input()
	cardTemplate: (item: any) => string;

	@Input()
	paging: boolean | GuiListPaging;

	@Input()
	view: GuiListView;

	@Input()
	fields: Array<GuiListField>;

	@Input()
	searching: GuiListSearching;

	@Output()
	pageChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	pageSizeChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	searchPhraseChanged: EventEmitter<string> = new EventEmitter<string>();

	containerTemplate: ListViewTemplate;

	listCardTemplate: ListViewCardTemplate;

	listViewMode: ListViewMode;

	listViewModeSelector: boolean;

	guiListPagingConverter: GuiListPagingConverter = new GuiListPagingConverter();

	guiListModeConverter: GuiListModeConverter = new GuiListModeConverter();

	listFields: Array<FieldConfig>;

	searchConfig: SearchConfig;

	ngOnChanges(changes: SimpleChanges) {

		const templateRef = this.listItem ? this.listItem.templateRef : null;

		this.containerTemplate = new ListViewTemplate(this.template, templateRef);

		const cardTemplateRef = this.listCard ? this.listCard.templateRef : null;

		this.listCardTemplate = new ListViewCardTemplate(this.cardTemplate, cardTemplateRef);

		if (changes.paging !== undefined && changes.paging.currentValue !== undefined) {

			if (typeof this.paging !== 'boolean') {
				this.paging = this.guiListPagingConverter.convert(this.paging);
			}
		}

		if (changes.view !== undefined && changes.view.currentValue !== undefined) {

			if (this.view.active !== undefined) {
				this.listViewMode = this.guiListModeConverter.convert(this.view.active);
			}

			if (this.view.selector !== undefined) {
				this.listViewModeSelector = this.view.selector;
			}

		}

		if (changes.fields !== undefined && changes.fields.currentValue !== undefined) {

			this.listFields = this.fields.map((f) => {
				return {
					field: f.field,
					type: f.type as any // DataType
				};
			});
		}

		if (changes.searching !== undefined && changes.searching.currentValue !== undefined) {

			this.searchConfig = this.searching as SearchConfig;
		}

	}

}
