import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { RowSelectToggleType } from '../core/row-select-toggle-type';

export class ToggleSelectedRowCommand extends Command {

	constructor(structureId: StructureId,
				private readonly selectedRow: string,
				private readonly type: RowSelectToggleType) {
		super(structureId, 'ToggleSelectedRowCommand');
	}

	getSelectedRow(): string {
		return this.selectedRow;
	}

	getType(): RowSelectToggleType {
		return this.type;
	}

}
