import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { SelectionModeSetEvent } from './selection-mode-set.event';
import { RowSelectionMode } from '../../../domain-api/row-selection';

export class SelectionModeSetAggregateEvent extends AggregateEvent<StructureId> {

	constructor(structureId: StructureId,
				private readonly mode: RowSelectionMode) {
		super(structureId, 'SelectionModeSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new SelectionModeSetEvent(this.getAggregateId(), this.mode);
	}
}
