import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { UniqueFilterUnselectedEvent } from './unique-filter-unselected.event';


export class UniqueFilterUnselectedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: StructureId) {
		super(aggregateId, 'UniqueFilterUnselectedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new UniqueFilterUnselectedEvent(
			this.getAggregateId()
		);
	}

}
