import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';
import { Field } from '../core/field/field';
import { FieldConfig } from '../../domain-api/field.config';


export class FieldsInitedEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				public readonly fieldConfigs: Array<FieldConfig>, // TODO merge
				private readonly fields: Array<Field>) {
		super(aggregateId, 'FieldsInitedEvent');
	}

	getFields(): Array<Field> {
		return this.fields;
	}
}
