import { Command } from '@generic-ui/hermes';

import { ListViewId } from '../../list-view.id';


export class ToggleListViewSelectorCommand extends Command {

	constructor(listViewId: ListViewId,
				private readonly enabled: boolean) {
		super(listViewId, 'ToggleListViewSelectorCommand');
	}

	iseEnabled(): boolean {
		return this.enabled;
	}

}
