import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { ListViewCreatedEvent } from './list-view-created.event';
import { ListViewId } from '../list-view.id';

export class ListViewCreatedAggregateEvent extends AggregateEvent<ListViewId> {

	constructor(aggregateId: ListViewId) {
		super(aggregateId, 'ListViewCreatedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<ListViewId> {
		return new ListViewCreatedEvent(this.getAggregateId());
	}
}
