import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FieldReadModel } from '../../../field/domain-api/read/field.read-model';

@Component({
	selector: 'gui-field-selector[fields]',
	template: `

		Field:

		<gui-dropdown>
			<gui-dropdown-item *ngFor="let field of fields"
							   (click)="onSelectChange(field)">
				{{field.getName()}}
			</gui-dropdown-item>
		</gui-dropdown>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FieldSelectorComponent {

	@Input()
	fields: ReadonlyArray<FieldReadModel>;

	@Output()
	fieldSelected = new EventEmitter<FieldReadModel>();

	onSelectChange(field: FieldReadModel): void {
		this.fieldSelected.emit(field);
	}
}
