import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { ListViewModeSetEvent } from './list-view-mode-set.event';
import { ListViewId } from '../list-view.id';
import { ListViewMode } from './list-view-mode';

export class ListViewModeSetAggregateEvent extends AggregateEvent<ListViewId> {

	constructor(listViewId: ListViewId,
				private readonly mode: ListViewMode) {
		super(listViewId, 'ListViewModeSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<ListViewId> {
		return new ListViewModeSetEvent(this.getAggregateId(), this.mode);
	}
}
