import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit, ViewEncapsulation, SimpleChanges, OnChanges, ElementRef, Renderer2 } from '@angular/core';

import { CellTemplateWithAccessor } from '../../../../../composition/domain-api/read/definition/cell-template-with-accessor';
import { ItemEntity } from '../../../../source/domain/core/item/item.entity';
import { FormationCommandInvoker } from '../../../../source/domain-api/formation/formation.command-invoker';
import { StructureId } from '../../../domain/structure.id';
import { RowSelectToggleType } from '../../../../source/domain/formation/core/row-select-toggle-type';
import { FormationWarehouse } from '../../../../source/domain-api/formation/formation.warehouse';
import { SmartComponent } from '../../../../../common/cdk/smart-component';
import { RowSelectedReadModel } from '../../../../source/domain-api/formation/row-selected.read-model';


@Component({
	selector: 'gui-structure-row',
	templateUrl: `./structure-row.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureRowComponent extends SmartComponent implements OnChanges, OnInit {

	@Input()
	entity: ItemEntity;

	@Input()
	columns: Array<CellTemplateWithAccessor>;

	@Input()
	editMode: boolean;

	@Input()
	cellEditing: boolean;

	@Input()
	searchPhrase: string;

	@Input()
	index: number;

	@Input()
	detailsEnabled: boolean = false;

	@Input()
	checkboxSelection: boolean = false;

	selectedItem: boolean = false;

	private row: RowSelectedReadModel;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly elRef: ElementRef,
				private readonly renderer2: Renderer2,
				private readonly structureId: StructureId,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly formationCommandDispatcher: FormationCommandInvoker) {
		super();
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.entity !== undefined && changes.entity.currentValue !== undefined) {
			this.checkSelectedItem();
		}
	}

	ngOnInit() {

		this.formationWarehouse
			.onRowSelectedReadModel(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((row: RowSelectedReadModel) => {
				this.row = row;
				let prevValue = this.selectedItem;

				this.checkSelectedItem();

				if (prevValue !== this.selectedItem) {

					if (prevValue) {
						this.renderer2.removeClass(this.elRef.nativeElement, 'selected');
					} else {
						this.renderer2.addClass(this.elRef.nativeElement, 'selected');
					}

					this.changeDetectorRef.detectChanges();
				}
			});
	}

	trackByFn(): number {
		return 0;
	}

	selectRow(): void {
		event.stopPropagation();
		this.formationCommandDispatcher.toggleSelectedRow(this.entity.getId(), RowSelectToggleType.ADD, this.structureId);
	}

	checkSelectedItem(): void {
		if (this.row) {

			let prevValue = this.selectedItem;

			this.selectedItem = this.row.isSelected(this.entity.getId());

			if (prevValue !== this.selectedItem) {

				if (prevValue) {
					this.renderer2.removeClass(this.elRef.nativeElement, 'selected');
				} else {
					this.renderer2.addClass(this.elRef.nativeElement, 'selected');
				}
			}
		}
	}

}
