import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ViewEncapsulation } from '@angular/core';
import { SearchWarehouse } from '../../../../search/domain-api/search.warehouse';
import { SmartComponent } from '../../../../../common/cdk/smart-component';
import { StructureId } from '../../../../core/domain/structure.id';
import { SearchCommandInvoker } from '../../../../search/domain-api/search.command-invoker';

@Component({
	selector: 'gui-active-search',
	template: `

		<ng-container *ngIf="phrase">
			<div>
				Active search by:
			</div>

			<div>
				<gui-chip>{{phrase}}</gui-chip>
			</div>

			<div>
				<button gui-button
						[outline]="true"
						[primary]="true"
						(click)="clearSearch()">
					Clear search
				</button>
			</div>
		</ng-container>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class ActiveSearchComponent extends SmartComponent {

	phrase: string;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly searchCommandDispatcher: SearchCommandInvoker,
				private readonly searchWarehouse: SearchWarehouse) {
		super();
	}

	ngOnInit() {
		this.searchWarehouse
			.onPhrase(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((phrase: string) => {
				this.phrase = phrase;
				this.changeDetectorRef.detectChanges();
			});
	}

	clearSearch(): void {
		event.stopPropagation();
		this.searchCommandDispatcher.search('', this.structureId);
	}

}
