import { SimpleChanges } from '@angular/core';

import { SmartComponent } from './smart-component';

export abstract class Gate extends SmartComponent {

	protected constructor() {
		super();
	}

	isDefined(propertyName: string, simpleChanges: SimpleChanges): boolean {
		return simpleChanges[propertyName] !== undefined && simpleChanges[propertyName].currentValue !== undefined;
	}

}
