import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { AllUniqueFilterUnselectedEvent } from './all-unique-filter-unselected.event';


export class AllUniqueFilterUnselectedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: StructureId) {
		super(aggregateId, 'AllUniqueFilterUnselectedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new AllUniqueFilterUnselectedEvent(
			this.getAggregateId()
		);
	}

}
