import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';
import { FieldConfig } from '../../domain-api/field.config';


export class InitFieldsCommand extends Command {

	constructor(structureId: StructureId,
				private readonly fieldConfigs: Array<FieldConfig>) {
		super(structureId, 'InitFieldsCommand');
	}

	getFieldConfigs(): Array<FieldConfig> {
		return this.fieldConfigs;
	}

}
