import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { Reactive } from '../../../../common/cdk/reactive';
import { ListViewMode } from '../../domain/mode/list-view-mode';
import { ListViewId } from '../../domain/list-view.id';


@Injectable()
export class ListViewModeRepository extends Reactive {

	private mode = new Map<string, ListViewMode>();

	private mode$ = new ReplaySubject<Map<string, ListViewMode>>();

	constructor() {
		super();
	}

	setMode(mode: ListViewMode, listViewId: ListViewId): void {
		this.next(mode, listViewId);
	}

	on(listViewId: ListViewId): Observable<ListViewMode> {

		return this.mode$
				   .asObservable()
				   .pipe(
					   this.takeUntil(),
					   map((map: Map<string, ListViewMode>) => {
						   return map.get(listViewId.toString());
					   }),
					   distinctUntilChanged()
				   );
	}

	private next(mode: ListViewMode, listViewId: ListViewId): void {
		this.mode.set(listViewId.toString(), mode);
		this.mode$.next(this.mode);
	}

}
