import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';
import { ActiveFilterReadModel } from '../../domain-api/active/active-filter.read-model';

export class ActiveFiltersSetEvent extends DomainEvent<StructureId> {

	constructor(structureId: StructureId,
				private readonly filters: ReadonlyArray<ActiveFilterReadModel>) {
		super(structureId, 'ActiveFiltersSetEvent');
	}

	getFilters(): ReadonlyArray<any> {
		return this.filters;
	}

}
