import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	Input,
	OnInit,
	PLATFORM_ID,
	Renderer2,
	ViewEncapsulation
} from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { combineLatest } from 'rxjs';

import { SmartComponent } from '../../../../common/cdk/smart-component';

import { CellTemplateWithAccessor } from '../../../../composition/domain-api/read/definition/cell-template-with-accessor';
import { FormationCommandInvoker } from '../../../source/domain-api/formation/formation.command-invoker';
import { StructureCellEditArchive } from '../edit/structure.cell-edit.archive';
import { StructureEditModeArchive } from '../edit/structure.edit-mode.archive';
import { FormationWarehouse } from '../../../source/domain-api/formation/formation.warehouse';
import { ItemEntity } from '../../../source/domain/core/item/item.entity';
import { VerticalFormationRepository } from '../../domain-api/vertical-formation/vertical-formation.repository';
import { StructureWarehouse } from '../../domain-api/structure.warehouse';
import { StructureId } from '../../domain/structure.id';
import { SearchPhraseRepository } from '../../../search/domain-api/phrase/search-phrase.repository';
import { SearchHighlightArchive } from '../../../search/domain-api/highlight/search-highlight.archive';
import { StructureVerticalFormationWarehouse } from '../../domain-api/vertical-formation/structure-vertical-formation.warehouse';
import { RowSelectionTypeArchive } from '../../../source/domain-api/formation/type/row-selection-type.archive';
import { RowSelectionType } from '../../../source/domain-api/row-selection';
import { RowSelectToggleType } from '../../../source/domain/formation/core/row-select-toggle-type';


@Component({
	selector: 'gui-structure-content',
	templateUrl: `structure-content.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContentComponent extends SmartComponent implements OnInit {

	@Input()
	source: Array<ItemEntity>;

	@Input()
	columns: Array<CellTemplateWithAccessor>;

	editMode = false;

	cellEditing = false;

	rowHeight: number;

	searchPhrase: string = '';

	highlighting: boolean = true;

	phrase: boolean = true;

	rowDetailOpened = 4;

	checkboxSelection: boolean = false;

	constructor(@Inject(PLATFORM_ID) private readonly platformId: any,
				private readonly renderer: Renderer2,
				private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly formationCommandService: FormationCommandInvoker,
				private readonly structureCellEditArchive: StructureCellEditArchive,
				private readonly structureEditModeArchive: StructureEditModeArchive,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly structureVerticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly verticalFormationRepository: VerticalFormationRepository,
				private readonly structureId: StructureId, // REfactor
				private readonly rowSelectionTypeArchive: RowSelectionTypeArchive,
				private readonly structureSearchPhraseRepository: SearchPhraseRepository,
				private readonly structureSearchHighlightArchive: SearchHighlightArchive) {
		super();
	}

	ngOnInit() {

		this.rowSelectionTypeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((type: RowSelectionType) => {
				this.checkboxSelection = type === RowSelectionType.CHECKBOX;
				this.changeDetectorRef.detectChanges();
			});

		// this.formationWarehouse
		// 	.onRowSelectedReadModel()
		// 	.pipe(
		// 		filter(() => !this.checkboxSelection),
		// 		this.takeUntil()
		// 	)
		// 	.subscribe((rowSelected: RowSelectedReadModel) => {
		//
		// 		this.clearSelectedRows();
		//
		// 		rowSelected.getAll()
		// 				   .forEach((id: string) => {
		//
		// 					   const strippedId = 'gui' + id.replace(/-/g, ''),
		// 						   selectedRowEl = this.elementRef.nativeElement.querySelector(`#${strippedId}`);
		//
		// 					   if (selectedRowEl) {
		// 						   this.renderer.addClass(selectedRowEl, 'selected');
		// 					   }
		// 				   });
		// 	});

		this.structureEditModeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.editMode = enabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureCellEditArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.cellEditing = enabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureVerticalFormationWarehouse
			.onRowHeight(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowHeight: number) => {
				this.rowHeight = rowHeight;
				this.changeDetectorRef.detectChanges();
			});

		combineLatest(
			this.structureSearchPhraseRepository
				.onPhrase(this.structureId),
			this.structureSearchHighlightArchive
				.onValue()
		).pipe(
			this.takeUntil()
		 )
		 .subscribe((args: Array<any>) => {

			 const searchPhrase = args[0],
				 highlighting = args[1];

			 this.highlighting = highlighting;
			 this.searchPhrase = searchPhrase;

			 if (!this.highlighting) {
				 this.searchPhrase = '';
			 }

			 this.changeDetectorRef.detectChanges();
		 });
	}

	trackByFn() {
		return 0;
	}

	translateY(index: number): string {

		let height = index * this.rowHeight;

		// if (index > this.rowDetailOpened) {
		// 	height += 200;
		// }

		return `translateY(${height}px)`;
	}

	toggleSelectedRow(entity: ItemEntity): void {
		if (!this.checkboxSelection) {
			this.formationCommandService.toggleSelectedRow(entity.getId(), RowSelectToggleType.NONE);
		}
	}

	private clearSelectedRows(): void {
		const selectedElements = this.elementRef.nativeElement.querySelectorAll('.selected');

		if (isPlatformBrowser(this.platformId)) {
			selectedElements.forEach((el: any) => {
				this.renderer.removeClass(el, 'selected');
			});
		}

	}

}
