import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';

import { CellTemplateWithContext } from '../../../../composition/domain-api/read/definition/cell-template-with-context';
import { SmartComponent } from '../../../../common/cdk/smart-component';
import { CompositionWarehouse } from '../../../../composition/domain-api/composition.warehouse';
import { FieldWarehouse } from '../../../field/domain-api/field.warehouse';
import { StructureId } from '../../../core/domain/structure.id';
import { FieldReadModel } from '../../../field/domain-api/read/field.read-model';
import { FilterWarehouse } from '../../domain-api/filter.warehouse';
import { FilterTypeMap } from '../../domain-api/type/filter-type-map';
import { FilterTypeReadModel } from '../../domain-api/type/filter-type.read-model';
import { FilterTypeId } from '../../domain/type/filter-type.id';
import { FilterCommandInvoker } from '../../domain-api/filter.command-invoker';
import { FieldId } from '../../../field/domain/core/field/field.id';
import { ActiveFilterReadModel } from '../../domain-api/active/active-filter.read-model';


@Component({
	selector: 'gui-filter-menu',
	template: `

		<div>
			<gui-active-filter-list></gui-active-filter-list>
		</div>

		<!--		<div>-->
		<!--			<gui-column-selector-->
		<!--					[columns]="columns"-->
		<!--					(columnSelected)="onColumnSelect($event)">-->
		<!--			</gui-column-selector>-->

		<!--			<ng-container *ngIf="selectedColumn">-->
		<!--				{{selectedColumn.getFieldId()}}-->
		<!--			</ng-container>-->
		<!--		</div>-->

		<div>
			<gui-field-selector
					[fields]="fields"
					(fieldSelected)="onFieldSelect($event)">
			</gui-field-selector>

			<ng-container *ngIf="selectedColumn">
				{{selectedColumn.getFieldId()}}
			</ng-container>
		</div>

		<div>
			<gui-filter-type-selector
					[filterTypes]="filterTypes"
					(filterTypeSelected)="onFilterTypeSelect($event)">
			</gui-filter-type-selector>

			<ng-container *ngIf="selectedFilterTypeId">
				{{selectedFilterTypeId.toString()}}
			</ng-container>
		</div>

		<div>

			<gui-filter-value *ngIf="selectedFilterTypeId"
							  (valueChanged)="onValueChanged($event)">
			</gui-filter-value>

		</div>

		<div>
			<button gui-button
					[primary]="true"
					[disabled]="!selectedFilterTypeId"
					(click)="addFilter()">
				Filter
			</button>
		</div>

		<div>
			<button gui-button
					[secondary]="true"
					(click)="removeAllFilters()">
				Clear filters
			</button>
		</div>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-filter-menu]': 'true'
	}
})
export class FilterMenuComponent extends SmartComponent implements OnInit {

	selectedColumn: CellTemplateWithContext;

	columns: Array<CellTemplateWithContext> = [];

	selectedField: FieldReadModel;

	fields: Array<FieldReadModel> = [];

	filterTypeMap: FilterTypeMap;

	filterTypes: Array<FilterTypeReadModel>;

	selectedFilterTypeId: FilterTypeId;

	selectedValue: string;

	activeFilters: Array<ActiveFilterReadModel> = [];

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly fieldWarehouse: FieldWarehouse,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly filterCommandDispatcher: FilterCommandInvoker,
				private readonly compositionWarehouse: CompositionWarehouse) {
		super();
	}

	ngOnInit() {

		this.filterWarehouse
			.onActiveFilters(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((activeFilters: Array<ActiveFilterReadModel>) => {
				this.activeFilters = activeFilters;
				this.changeDetectorRef.detectChanges();
			});

		this.filterWarehouse
			.onFilterTypes(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((filterTypeMap: FilterTypeMap) => {
				this.filterTypeMap = filterTypeMap;
				this.changeDetectorRef.detectChanges();
			});

		this.fieldWarehouse
			.onFields(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((fieldReadModels: Array<FieldReadModel>) => {
				this.fields = fieldReadModels;
				this.changeDetectorRef.detectChanges();
			});

		this.compositionWarehouse
			.onHeaderColumns()
			.pipe(this.takeUntil())
			.subscribe((columns: Array<CellTemplateWithContext>) => {
				this.columns = columns;
				this.changeDetectorRef.detectChanges();
			});
	}

	onColumnSelect(selectedColumn: CellTemplateWithContext): void {

		this.selectedColumn = selectedColumn;

		this.filterTypes = this.filterTypeMap.getFilterTypes(this.selectedColumn.getFieldId());

		this.changeDetectorRef.detectChanges();
	}

	onFieldSelect(field: FieldReadModel): void {
		this.selectedField = field;

		this.filterTypes = this.filterTypeMap.getFilterTypes(this.selectedField.getFieldId());

		this.changeDetectorRef.detectChanges();
	}

	onFilterTypeSelect(filterTypeId: FilterTypeId): void {

		this.selectedFilterTypeId = filterTypeId;

		this.changeDetectorRef.detectChanges();
	}

	removeAllFilters(): void {
		this.filterCommandDispatcher.removeAllFilters(this.structureId);
	}

	addFilter(): void {

		const fieldId: FieldId = this.selectedField.getFieldId(),
			filterTypeId: FilterTypeId = this.selectedFilterTypeId,
			value: any = this.selectedValue;

		this.filterCommandDispatcher.add(fieldId, filterTypeId, value, this.structureId);

		this.clearAddFilterForm();
	}

	onValueChanged(value: string): void {
		this.selectedValue = value;
	}

	clearAddFilterForm(): void {
		this.selectedColumn = null;
		this.selectedField = null;
		this.selectedFilterTypeId = null;
		this.selectedValue = null;
		this.changeDetectorRef.detectChanges();
	}
}
