import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { ListViewModeRepository } from './list-view.mode.repository';
import { ListViewId } from '../../domain/list-view.id';
import { ListViewModeSetEvent } from '../../domain/mode/list-view-mode-set.event';


@Injectable()
export class ListViewModeSetEventHandler implements DomainEventHandler<ListViewId, ListViewModeSetEvent> {

	constructor(private listViewModeRepository: ListViewModeRepository) {
	}

	forEvent(): DomainEventType<ListViewModeSetEvent> {
		return ListViewModeSetEvent;
	}

	handle(event: ListViewModeSetEvent): void {

		if (event.ofMessageType('ListViewModeSetEvent')) {
			this.listViewModeRepository.setMode(event.getTheme(), event.getAggregateId());
		}

	}

}
