export interface GuiListPaging {

	enabled?: boolean;

	page?: number;

	pageSize?: number;

	pageSizes?: Array<number>;

	pagerTop?: boolean;

	pagerBottom?: boolean;

}

export enum GuiListMode {
	LIST,
	CARD
}

export interface GuiListView {

	active?: GuiListMode;

	selector?: boolean;

}

export interface GuiListField {

	field?: string;

	type?: GuiListFieldType;

}

export enum GuiListFieldType {
	UNKNOWN,
	NUMBER,
	STRING,
	BOOLEAN,
	DATE,
	CUSTOM
}

export interface GuiListSearching {

	enabled?: boolean;

	placeholder?: string;

	phrase?: string;

}
