import { ChangeDetectionStrategy, Component, ElementRef, ViewEncapsulation } from '@angular/core';

import { ListViewGateway } from './gateway/list-view.gateway';

import { StructureId } from '../../core/domain/structure.id';
import { StructureIdGenerator } from '../../core/feature/structure-id.generator';
import { structureIdFactory } from '../../core/feature/structure.component';
import { StructureCommandDispatcher } from '../../core/domain-api/structure.command-dispatcher';
import { SourceCommandInvoker } from '../../source/domain-api/source.command-invoker';
import { listViewProviders } from './list-view.providers';
import { PagingFeatureModule } from '../../paging/feature/paging.feature-module';
import { provideComponentServices } from '../../core/feature/local/local-providers';
import { PagingCommandInvoker } from '../../paging/domain-api/paging.command-invoker';
import { ListViewTemplateArchive } from './source/template/list-view-template.archive';
import { ListViewCardTemplateArchive } from './card/template/list-view.card-template.archive';
import { ListViewReadModelRootId } from '../domain-api/read/list-view.read-model-root-id';
import { ListViewCommandInvoker } from '../domain-api/list-view.command-invoker';
import { filterContainerToken } from '../../filter/feature/config/filter-container-token';
import { FilterContainerRef } from '../../filter/feature/config/filter-container-ref';


/** @internal */
export function listViewIdFactory(generator: StructureIdGenerator): ListViewReadModelRootId {
	return new ListViewReadModelRootId('gui-' + generator.generateId());
}

const componentProviders = [{
	provide: StructureId,
	useFactory: structureIdFactory,
	deps: [
		StructureIdGenerator
	]
}, {
	provide: ListViewReadModelRootId,
	useFactory: listViewIdFactory,
	deps: [
		StructureIdGenerator
	]
},
	...listViewProviders,

	...provideComponentServices(),

	PagingFeatureModule.forComponent()
];

@Component({
	selector: 'gui-list-view',
	template: `

		<gui-list-view-layout></gui-list-view-layout>
	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	providers: [
		...componentProviders,
		{
			provide: filterContainerToken,
			useExisting: ListViewComponent
		}
	],
	styleUrls: [
		'./style/list-view.ngx.scss',
		'./source/list-view-source.component.ngx.scss',
		'./style/theme/generic.list.ngx.scss'
	],
	host: {
		'[class.gui-list-view]': `"true"`,
		'[class.gui-generic]': `"true"`,
		'[id]': 'structureId.toString()'
	}
})
export class ListViewComponent extends ListViewGateway implements FilterContainerRef {

	constructor(public readonly structureId: StructureId,
				public readonly listViewReadModelRootId: ListViewReadModelRootId,
				private readonly elementRef: ElementRef,
				sourceCommandService: SourceCommandInvoker,
				containerTemplateArchive: ListViewTemplateArchive,
				listCardTemplateArchive: ListViewCardTemplateArchive,
				structureCommandService: StructureCommandDispatcher,
				structurePagingCommandDispatcher: PagingCommandInvoker,
				listViewCommandDispatcher: ListViewCommandInvoker
	) {
		super(
			structureId,
			sourceCommandService
		);
		structureCommandService.createStructure(this.structureId);
		listViewCommandDispatcher.create(this.listViewReadModelRootId);
	}

	getElementRef(): ElementRef {
		return this.elementRef;
	}

}
