import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';

import { SourceWarehouse } from '../../../source/domain-api/source.warehouse';
import { SmartComponent } from '../../../../common/cdk/smart-component';
import { ItemEntity } from '../../../source/domain/core/item/item.entity';
import { StructureId } from '../../../core/domain/structure.id';
import { ListViewCardTemplate } from './template/list-view.card-template';
import { ListViewCardTemplateArchive } from './template/list-view.card-template.archive';

@Component({
	selector: 'gui-list-container-card',
	template: `

		<gui-list-card-item *ngFor="let element of items"
							[item]="element"
							[template]="cardTemplate">
		</gui-list-card-item>

		<gui-empty-source [items]="items">
		</gui-empty-source>

	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	host: {
		'[class.gui-list-container-card]': `"true"`
	}
})
export class ListViewContainerCardComponent extends SmartComponent implements OnInit {

	items: Array<any> = [];

	cardTemplate: ListViewCardTemplate;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly structureSourceWarehouse: SourceWarehouse,
				private readonly listCardTemplateArchive: ListViewCardTemplateArchive) {
		super();
	}

	ngOnInit() {
		this.structureSourceWarehouse
			.onEntities(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((items: Array<ItemEntity>) => {
				this.items = items.map(i => i.getData());
				this.changeDetectorRef.detectChanges();
			});

		this.listCardTemplateArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((template: ListViewCardTemplate) => {
				this.cardTemplate = template;
				this.changeDetectorRef.detectChanges();
			});
	}

}
