import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { RowSelectionMode } from '../../../domain-api/row-selection';

export class SelectionModeSetEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly mode: RowSelectionMode) {
		super(aggregateId, 'SelectionModeSetEvent');
	}

	getMode(): RowSelectionMode {
		return this.mode;
	}

}
