import { Injectable } from '@angular/core';

import { DomainEventBus, EventRepository } from '@generic-ui/hermes';

import { ListViewId } from '../domain/list-view.id';
import { ListViewReadModelRootId } from './read/list-view.read-model-root-id';
import { ListViewModeSetEvent } from '../domain/mode/list-view-mode-set.event';

@Injectable()
export class ListViewEventRepository extends EventRepository<ListViewReadModelRootId, ListViewId> {

	constructor(domainEventBus: DomainEventBus) {
		super(domainEventBus);
	}

	onModeChange(listViewReadModelRootId: ListViewReadModelRootId) {

		return this.onEvent(listViewReadModelRootId, ListViewModeSetEvent);
	}


}
