import { TemplateRef } from '@angular/core';

export class ListViewCardTemplate {

	constructor(private readonly template: (item: any) => string,
				private readonly templRef: TemplateRef<any>) {
	}

	hasTemplateRef(): boolean {
		return !!this.templRef;
	}

	getTemplateRef(): TemplateRef<any> {
		return this.templRef;
	}

	getTemplateMethod(): (item: any) => string {
		return this.template;
	}
}
