import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { UniqueFilterSelectedEvent } from './unique-filter-selected.event';


export class UniqueFilterSelectedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: StructureId) {
		super(aggregateId, 'UniqueFilterSelectedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new UniqueFilterSelectedEvent(
			this.getAggregateId()
		);
	}

}
