import { Directive, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';

import { PagingConfig } from '../../../../paging/domain-api/paging-config';
import { PagingCommandInvoker } from '../../../../paging/domain-api/paging.command-invoker';
import { PagingEventRepository } from '../../../../paging/domain-api/paging.event-repository';
import { StructureId } from '../../../../core/domain/structure.id';
import { Gate } from '../../../../../common/cdk/gate';


@Directive({
	selector: 'gui-list-view[paging]'
})
export class ListViewPagingGate extends Gate implements OnChanges, OnInit {

	@Input()
	paging: PagingConfig;

	@Output()
	pageChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	pageSizeChanged: EventEmitter<number> = new EventEmitter<number>();

	constructor(private readonly structureId: StructureId,
				private readonly structurePagingCommandDispatcher: PagingCommandInvoker,
				private readonly pagingEventRepository: PagingEventRepository) {
		super();
	}

	ngOnChanges(simpleChanges: SimpleChanges) {

		if (this.isDefined('paging', simpleChanges)) {

			let pagingConfig: PagingConfig;

			if (typeof this.paging === 'boolean') {
				pagingConfig = {
					enabled: this.paging
				};
			} else {
				pagingConfig = this.paging;

				// if (this.paging.displayMode !== undefined) {
				// 	this.structurePagingDisplayModeArchive.next(this.paging.displayMode);
				// }
			}

			this.structurePagingCommandDispatcher.setPaging(pagingConfig);
		}
	}

	ngOnInit() {

		this.pagingEventRepository
			.onPageChange(this.structureId.toReadModelRootId())
			.pipe(this.takeUntil())
			.subscribe((pageNumber: number) => {
				this.pageChanged.emit(pageNumber);
			});

		this.pagingEventRepository
			.onPageSizeChange(this.structureId.toReadModelRootId())
			.pipe(this.takeUntil())
			.subscribe((pageSize: number) => {
				this.pageSizeChanged.emit(pageSize);
			});
	}

	onPageChange(page: number): void {
		this.pageChanged.emit(page);
	}

}
