import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { ListViewSelectorRepository } from './list-view.selector.repository';

import { ListViewId } from '../../../domain/list-view.id';
import { ListViewSelectorToggledEvent } from '../../../domain/mode/selector/list-view-selector-toggled.event';


@Injectable()
export class ListViewSelectorToggledEventHandler implements DomainEventHandler<ListViewId, ListViewSelectorToggledEvent> {

	constructor(private listViewSelectorRepository: ListViewSelectorRepository) {
	}

	forEvent(): DomainEventType<ListViewSelectorToggledEvent> {
		return ListViewSelectorToggledEvent;
	}

	handle(event: ListViewSelectorToggledEvent): void {

		if (event.ofMessageType('ListViewSelectorToggledEvent')) {
			this.listViewSelectorRepository.setMode(event.isEnabled(), event.getAggregateId());
		}
	}

}
