import { Command } from '@generic-ui/hermes';

import { ListViewMode } from './list-view-mode';
import { ListViewId } from '../list-view.id';


export class SetListViewModeCommand extends Command {

	constructor(listViewId: ListViewId,
				private readonly mode: ListViewMode) {
		super(listViewId, 'SetListViewModeCommand');
	}

	getMode(): ListViewMode {
		return this.mode;
	}

}
