import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { Reactive } from '@generic-ui/hermes';

import { ListViewId } from '../../../domain/list-view.id';


@Injectable()
export class ListViewSelectorRepository extends Reactive {

	private selectorEnabled = new Map<string, boolean>();

	private selectorEnabled$ = new ReplaySubject<Map<string, boolean>>();

	constructor() {
		super();
	}

	setMode(selectorEnabled: boolean, listViewId: ListViewId): void {
		this.next(selectorEnabled, listViewId);
	}

	on(listViewId: ListViewId): Observable<boolean> {

		return this.selectorEnabled$
				   .asObservable()
				   .pipe(
					   this.takeUntil(),
					   map((map: Map<string, boolean>) => {
						   return map.get(listViewId.toString());
					   }),
					   distinctUntilChanged()
				   );
	}

	private next(enabled: boolean, listViewId: ListViewId): void {
		this.selectorEnabled.set(listViewId.toString(), enabled);
		this.selectorEnabled$.next(this.selectorEnabled);
	}

}
