import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { AllUniqueFilterSelectedEvent } from './all-unique-filter-selected.event';


export class AllUniqueFilterSelectedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: StructureId) {
		super(aggregateId, 'AllUniqueFilterSelectedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new AllUniqueFilterSelectedEvent(
			this.getAggregateId()
		);
	}

}
