import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';

export class SelectedRowChangedEvent extends DomainEvent<StructureId> {

	constructor(structureId: StructureId,
				private readonly selectedRows: Array<string>,
				private readonly allSelected: boolean,
				private readonly allUnselected: boolean) {
		super(structureId, 'SelectedRowChangedEvent');
	}

	getSelectedRows(): Array<string> {
		return this.selectedRows;
	}

	isAllSelected(): boolean {
		return this.allSelected;
	}

	isAllUnselected(): boolean {
		return this.allUnselected;
	}
}
