import { Command } from '@generic-ui/hermes';

import { FieldId } from '../../../field/domain/core/field/field.id';

import { StructureId } from '../../../core/domain/structure.id';


export class ToggleFilterCommand extends Command {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly externalFilterId: string,
				private readonly filterValue: string) {
		super(structureId, 'ToggleFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getExternalFilterId(): string {
		return this.externalFilterId;
	}

	getFilterValue(): string {
		return this.filterValue;
	}

}
