import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../core/domain/structure.id';

export class SetSearchPhraseCommand extends Command {

	constructor(structureId: StructureId,
				private readonly phrase: string,
				private readonly initial: boolean) {
		super(structureId, 'SetSearchPhraseCommand');
	}

	getPhrase(): string {
		return this.phrase;
	}

	isInitial(): boolean {
		return this.initial;
	}

}
