import { Directive, Input, OnChanges, SimpleChanges } from '@angular/core';
import { StructureId } from '../../../../core/domain/structure.id';
import { Gate } from '../../../../../common/cdk/gate';
import { FieldConfig } from '../../../../field/domain-api/field.config';
import { FieldCommandInvoker } from '../../../../field/domain-api/field.command-invoker';


@Directive({
	selector: 'gui-list-view[fields]'
})
export class ListViewFieldGate extends Gate implements OnChanges {

	@Input()
	fields: Array<FieldConfig>;

	constructor(private readonly structureId: StructureId,
				private readonly fieldCommandDispatcher: FieldCommandInvoker) {
		super();
	}

	ngOnChanges(simpleChanges: SimpleChanges) {

		if (this.isDefined('fields', simpleChanges)) {

			this.fieldCommandDispatcher.initFields(this.fields, this.structureId);
		}
	}

}
