import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { ListViewSelectorToggledEvent } from './list-view-selector-toggled.event';
import { ListViewId } from '../../list-view.id';


export class ListViewSelectorToggledAggregateEvent extends AggregateEvent<ListViewId> {

	constructor(listViewId: ListViewId,
				private readonly enabled: boolean) {
		super(listViewId, 'ListViewSelectorToggledAggregateEvent');
	}

	toDomainEvent(): DomainEvent<ListViewId> {
		return new ListViewSelectorToggledEvent(this.getAggregateId(), this.enabled);
	}
}
