import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { UniqueFilterCalculatedEvent } from './unique-filter-calculated.event';
import { UniqueValue } from '../unique-value';

export class UniqueFilterCalculatedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly map: Map<string, Array<UniqueValue>>) {
		super(aggregateId, 'UniqueFilterCalculatedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new UniqueFilterCalculatedEvent(
			this.getAggregateId(),
			this.map
		);
	}

}
