import { Directive, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { StructureId } from '../../../../core/domain/structure.id';
import { Gate } from '../../../../../common/cdk/gate';
import { SearchConfig } from '../../../../search/domain-api/search-config';
import { SearchCommandInvoker } from '../../../../search/domain-api/search.command-invoker';
import { SearchEventRepository } from '../../../../search/domain-api/search.event-repository';


@Directive({
	selector: 'gui-list-view[searching]'
})
export class ListViewSearchingGate extends Gate implements OnChanges, OnInit {

	@Input()
	searching: SearchConfig;

	@Output()
	searchPhraseChanged: EventEmitter<string> = new EventEmitter();

	constructor(private readonly structureId: StructureId,
				private readonly searchCommandDispatcher: SearchCommandInvoker,
				private readonly searchEventRepository: SearchEventRepository) {
		super();
	}

	ngOnChanges(simpleChanges: SimpleChanges) {

		if (this.isDefined('searching', simpleChanges)) {
			this.searchCommandDispatcher.setSearchingConfig(this.searching, this.structureId);
		}
	}

	ngOnInit() {
		this.searchEventRepository
			.onSearchPhrase(this.structureId.toReadModelRootId())
			.pipe(
				this.takeUntil()
			)
			.subscribe((phrase: string) => {
				this.searchPhraseChanged.emit(phrase);
			});
	}

}
