import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/domain/structure.id';
import { FieldId } from '../../../../field/domain/core/field/field.id';
import { UniqueValueId } from '../unique-value-id';


export class SelectUniqueFilterCommand extends Command {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly uniqueValueId: UniqueValueId) {
		super(structureId, 'SelectUniqueFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getUniqueValueId(): UniqueValueId {
		return this.uniqueValueId;
	}

}
