import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { CellTemplateWithContext } from '../../../../composition/domain-api/read/definition/cell-template-with-context';

@Component({
	selector: 'gui-column-selector[columns]',
	template: `

		Column:

		<gui-dropdown>
			<gui-dropdown-item *ngFor="let column of columns"
							   (click)="onSelectChange(column)">
				<ng-container
						*ngTemplateOutlet="column.viewTemplate;
						   context: column.context">
				</ng-container>
			</gui-dropdown-item>
		</gui-dropdown>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class ColumnSelectorComponent {

	@Input()
	columns: Array<CellTemplateWithContext>;

	@Output()
	columnSelected = new EventEmitter<CellTemplateWithContext>();

	onSelectChange(column: CellTemplateWithContext): void {
		this.columnSelected.emit(column);
	}
}
