import { Injectable } from '@angular/core';

import { CommandInvoker } from '@generic-ui/hermes';

import { ListViewDispatcher } from '../domain/list-view.dispatcher';
import { listViewGlobalId } from './read/list-view.global-id';
import { ListViewReadModelRootId } from './read/list-view.read-model-root-id';
import { ListViewMode } from '../domain/mode/list-view-mode';


@Injectable()
export class ListViewCommandInvoker implements CommandInvoker {

	constructor(private readonly listViewDispatcher: ListViewDispatcher) {
	}

	create(listViewReadModelRootId: ListViewReadModelRootId = listViewGlobalId): void {
		this.listViewDispatcher.create(listViewReadModelRootId.toAggregateId());
	}

	setMode(mode: ListViewMode, listViewReadModelRootId: ListViewReadModelRootId = listViewGlobalId): void {
		this.listViewDispatcher.setMode(mode, listViewReadModelRootId.toAggregateId());
	}

	toggleSelector(enabled: boolean, listViewReadModelRootId: ListViewReadModelRootId = listViewGlobalId): void {
		this.listViewDispatcher.toggleSelector(enabled, listViewReadModelRootId.toAggregateId());
	}

}
