import { DomainEvent } from '@generic-ui/hermes';

import { ListViewId } from '../../list-view.id';


export class ListViewSelectorToggledEvent extends DomainEvent<ListViewId> {

	constructor(schemaId: ListViewId,
				private readonly enabled: boolean) {
		super(schemaId, 'ListViewSelectorToggledEvent');
	}

	isEnabled(): boolean {
		return this.enabled;
	}
}
