import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	OnInit,
	Renderer2,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { debounceTime } from 'rxjs/operators';
import { Subscription } from 'rxjs';

import { SmartComponent } from '../../../common/cdk/smart-component';
import { StructureWarehouse } from '../../core/domain-api/structure.warehouse';
import { SearchPlaceholderArchive } from '../domain-api/placeholder/search-placeholder.archive';
import { StructureId } from '../../core/domain/structure.id';
import { StructureVerticalFormationWarehouse } from '../../core/domain-api/vertical-formation/structure-vertical-formation.warehouse';
import { SearchCommandInvoker } from '../domain-api/search.command-invoker';
import { SearchWarehouse } from '../domain-api/search.warehouse';


@Component({
	selector: 'gui-search-bar',
	template: `
		<ng-container *ngIf="searchingEnabled">
			<form #formRef
				  [formGroup]="searchForm">
				<gui-search-icon></gui-search-icon>
				<input formControlName="searchPhrase" [placeholder]="placeholder">
				<span *ngIf="searchForm.controls['searchPhrase'].value" class="gui-clear-search-icon" (click)="clear()"></span>
			</form>
		</ng-container>
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-search-bar]': `"true"`
	},
	styleUrls: [
		'./style/searching.ngx.scss'
	]
})
export class SearchComponent extends SmartComponent implements OnInit {

	private static readonly FORM_SEARCH_NAME = 'searchPhrase';

	@ViewChild('formRef', { static: false })
	formRef: ElementRef;

	searchForm: FormGroup;

	placeholder = 'Search';

	searchingEnabled: boolean = false;

	searchInputSubscription: Subscription;

	constructor(private readonly formBuilder: FormBuilder,
				private readonly renderer2: Renderer2,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureId: StructureId,
				private readonly searchCommandDispatcher: SearchCommandInvoker,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly searchWarehouse: SearchWarehouse,
				private readonly verticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly structureSearchPlaceholderArchive: SearchPlaceholderArchive) {
		super();

		let controlsConfig = {};
		controlsConfig[SearchComponent.FORM_SEARCH_NAME] = '';

		this.searchForm = this.formBuilder.group(controlsConfig);
	}

	ngOnInit() {
		this.observeChanges();

		this.structureSearchPlaceholderArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((placeholder: string) => {
				this.placeholder = placeholder;
				this.changeDetectorRef.detectChanges();
			});

		this.searchWarehouse
			.onPhrase(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((phrase: string) => {

				if (phrase === undefined) {
					phrase = null;
				}

				let controlsConfig = {};
				controlsConfig[SearchComponent.FORM_SEARCH_NAME] = phrase;

				if (phrase !== this.searchForm.get([SearchComponent.FORM_SEARCH_NAME]).value) {

					this.stopObserveChanges();
					this.searchForm.setValue(controlsConfig);
					this.observeChanges();
				}

			});

		this.searchWarehouse
			.onSearchEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((searchingEnabled: boolean) => {
				this.searchingEnabled = searchingEnabled;
				this.changeDetectorRef.detectChanges();
			});
	}

	clear(): void {
		this.searchForm.reset();
	}

	private observeChanges(): void {
		this.searchInputSubscription =
			this.searchForm
				.controls[SearchComponent.FORM_SEARCH_NAME]
				.valueChanges
				.pipe(
					debounceTime(200),
					this.takeUntil()
				)
				.subscribe((phrase: string) => {
					this.searchCommandDispatcher.search(phrase, this.structureId);
				});
	}

	private stopObserveChanges(): void {
		this.searchInputSubscription.unsubscribe();
	}
}
